<template>
  <div class="container">
    <h1>Tính lương</h1>
    <config-salary ref="configSalary" @edited="calculateSalary"></config-salary>
    <config-time ref="configTime" @edited="calculateSalary"></config-time>
    <div class="insurance">
        <h3>Bảo hiểm (%)</h3>
        <div class="insurance-input">
            <input type="number" @input="calculateSalary" min="0" max="100" step="0.001" v-model="baoHiem">
            <span>%</span>
        </div>
    </div>
    <h3>Kết quả</h3>
    <div class="result">
        
      <saraly-board :rows="salaryBoard"></saraly-board>
      <div class="total-salary">
          <h3>Tổng lương:  </h3>
          <h3>{{formatNumber(totalSalary)}} - {{baoHiem}}% = <span> {{formatNumber(totalSalary - totalSalary*baoHiem*0.01)}}</span></h3>
      </div>
    </div>
  </div>
</template>
<script>
import ConfigSalary from "./ConfigSalary.vue";
import ConfigTime from "./ConfigTime.vue";
import SaralyBoard from "./SaralyBoard.vue";
export default {
  name: "MainLayout",
  components: {
    ConfigSalary,
    ConfigTime,
    SaralyBoard,
  },
  data: function () {
    return {
      salaryBoard: [
        {
          name: "Lương cơ bản",
          describe: "",
          total: 0,
        },
        {
          name: "Lương làm thêm",
          describe: "",
          total: 0,
        },
        {
          name: "Lương làm đêm",
          describe: "",
          total: 0,
        },
        {
          name: "Lương làm đặc biệt",
          describe: "",
          total: 0,
        },
      ],
      totalSalary: 0,
      baoHiem: 3.335+0.8+0.13+4.5,

    };
  },
  methods: {
    calculateSalary() {
      console.log("listened from main layout...");
      let configSalary = this.$refs.configSalary.configSalary;
      let configTime = this.$refs.configTime.configTime;
      //   console.log(configSalary,configTime);
      this.salaryBoard[0].total = configSalary[0].val * configTime[0].val;
      this.salaryBoard[1].total =
        configSalary[0].val * configSalary[1].val * configTime[1].val;
      this.salaryBoard[2].total =
        configSalary[0].val * configSalary[2].val * configTime[2].val;
      this.salaryBoard[3].total =
        configSalary[0].val * configSalary[3].val * configTime[3].val;

      this.salaryBoard[0].describe = `${configSalary[0].val} x ${configTime[0].val}h`;
      this.salaryBoard[1].describe = `${configSalary[0].val} x ${configSalary[1].val} x ${configTime[1].val}h`;
      this.salaryBoard[2].describe = `${configSalary[0].val} x ${configSalary[2].val} x ${configTime[2].val}h`;
      this.salaryBoard[3].describe = `${configSalary[0].val} x ${configSalary[3].val} x ${configTime[3].val}h`;

      this.totalSalary = this.salaryBoard.reduce(
        (previousSum, currentRecord) => currentRecord.total + previousSum,
        0
      );
      
    },
    formatNumber(number){
        return new Intl.NumberFormat('en-US').format(number)
    }
  },
};
</script>
