<template>
    <table border="1">
        <thead>
            <tr>
                <th v-for="column in cols" :key="column">{{column.name}}</th>
            </tr>
            

        </thead>
        <tbody>
            <tr>
                <td v-for="column in cols" :key="column"><input type="number" min="0" @input="handleOnChange" v-model.number="column.val"/></td>
            </tr>
        </tbody>
    </table>
</template>
<script>
export default {
    name: "BaseConfigTable",
    props:{
        cols: Array
    },
    data: function(){
        return{
            
        }
    },
    methods:{
        handleOnChange(){
            this.$emit('edited')
        }
    }
    


   
}
</script>
<style>
  
    .config-table table{
       
        width: 100%;
        overflow-wrap: break-word;
        
    }
    
    table input{
        width: 100%;
        text-align: center;
        outline: none;
    }
    th{
        font-size: 16px;
        color: darkcyan
    }
    th, td{
        padding: 10px 5px;
        box-shadow: 0px 1px 1px 0px gray;
